<template>
   <div class="page-changeCity">
       <el-row>
           <select-addre />
       </el-row>
       <el-row>
           <hot title=":" :list="hotList" />
       </el-row>
       <el-row>
           <hot title=":" :list="rencentList" />
       </el-row>
       <el-row>
           <categorey />
       </el-row>
   </div>
</template>

<script>
import Categorey from '@/components/changeCity/categorey'
import Hot from '@/components/changeCity/hot'
import SelectAddre from '@/components/changeCity/selectAddre'
export default {
    components : {
        SelectAddre,
        Hot,
        Categorey
    },
    data (){
        return {
            hotList : ["","",""],
            rencentList : ["", "", ""]
        }
    }
}
</script>


