import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

// 
const state = {
  position: {},
  UserName: ''
}

// 
// mutation commit
// mutationactioncommit
const mutations = {
  // val 
  setPosition (state, val) {
    state.position = val
  },
  setUserName (state, val) {
    state.userName = val
  }
}

// mutation
const actions = {
  setPosition (state, val) {
    // 
    state.commit('setPosition', val)
  }
}

// actions  dispatch$store.dispatch(setPostiob , 'val')

// store

export default new Vuex.Store({
  state,
  mutations,
  actions
})



