<template>
  <div>
    <span class="name"></span>
    <change
      className="province"
      :list="provinceList"
      title=""
      :showWrapperActive="showProvince"
      @clickPro="clickPro"
      :value="provinceValue"
      @selectPro="selectPro"
    />
    <change
      className="city"
      :list="city"
      title=""
      :showWrapperActive="showCity"
      @clickPro="clickCity"
      :value="cityValue"
      @selectPro="selectCity"
      :disabled="disabled"
    />
    <span></span>
    <el-autocomplete
      v-model="searchWord"
      :fetch-suggestions="querySearchAsync"
      placeholder=""
      @select="handleSelect"
    ></el-autocomplete>
  </div>
</template>

<script>
import api from '@/api/index.js'
import Change from "./change.vue";
export default {
  data() {
    return {
      loading: "",
      searchList: [],
      searchWord: "",
      showCity: false,
      showProvince: false,
      disabled: true,
      city: [],
      provinceValue: "",
      cityValue: "",
      provinceList: [],
    };
  },
  components: {
    Change,
  },
  created (){
    api.getProvinceList().then(res => {
      this.provinceList = res.data.data.map((item) => {
        item.name = item.provinceName
        return item
      })
    })
  },
  methods: {
    querySearchAsync(queryString) {
      // 
      // queryStringinput
      // console.log(queryString);
      // 
    },
    handleSelect() {
      // 
    },
    clickPro(boolean) {
      this.showProvince = boolean;
      if (this.showCity) {
        this.showCity = false;
      }
    },
    clickCity(boolean) {
      if(this.disabled){
        return;
      }
      this.showCity = boolean;
      if (this.showProvince) {
        this.showProvince = false;
      }
    },
    selectPro(item) {
      let value = item.name;
      this.disabled = false;
      // none
      this.showProvince = false;

      let list = [];
      this.provinceValue = value;
      // 
      this.provinceList.forEach((item) => {
        if (item.provinceName == value) {
          item.cityInfoList.forEach((i) => {
            list.push(i);
          });
          this.city = list;
        }
      });
    },
    selectCity(item) {
      this.cityValue = item.name;
      // 
      this.$store.dispatch('setPosition', item)
      this.$router.push({ name: "home" });
    },
  },
};
</script>

<style lang="scss">
@import "@/assets/css/changecity/iselect.scss";
</style>



